.carousel{
    height: 12rem;
}
.carousel div,.carousel div img{
    height: 12rem;
    width: 100%;
}
.carousel div img{
    position: absolute
}

.p_des_bg{
	overflow:hidden; background-color:#FFF; padding-bottom:80px;
}
.p_btm{
	width:100%; overflow:hidden; background-color:#4c4c4c; position: relative; left:0; bottom:0; z-index:900;
}
.p_btm ul li{
	width:25%; height:80px; overflow:hidden; color:#FFF; font-size:1.2em; text-align:center; float:left;
}
.p_btm ul li a{
	width:100%; height:80px; overflow:hidden; color:#FFF; float:left; text-align:center;
}
.p_btm ul li p{
	height:40px; overflow:hidden; margin:5% auto 3% auto; padding:0;
}
.p_btm ul li.b_1 p{
	background:url(/image/n_5.png) no-repeat center center;
}	
.p_btm ul li.b_2 p{
	background:url(/image/n_3.png) no-repeat center center;
}
.p_btm ul li.b_3{
	background-color:#f79f22; color:#FFF; line-height:80px;
}
.p_btm ul li.b_4{
	background-color:#ff4e00; color:#FFF; line-height:80px;
}

@-webkit-keyframes slideInUp {
	from {
	  -webkit-transform: translate3d(0, 100%, 0);
	  transform: translate3d(0, 100%, 0);
	  visibility: visible;
	}
  
	to {
	  -webkit-transform: translate3d(0, 0, 0);
	  transform: translate3d(0, 0, 0);
	}
}

@keyframes slideInUp {
	from {
	  -webkit-transform: translate3d(0, 100%, 0);
	  transform: translate3d(0, 100%, 0);
	  visibility: visible;
	}
  
	to {
	  -webkit-transform: translate3d(0, 0, 0);
	  transform: translate3d(0, 0, 0);
	}
}


/*begin  mask layer  begin*/
.black_overlay{
	width:100%; height:100%; background:rgba(255, 255, 255, .5) repeat center top; display:none; position:fixed; top:0%; left:0%; z-index:900;
}
.white_content{	
	width:85%; height:auto !important; min-height:100px; height:100px; background:url(/image/box.png) repeat center top; display:none; position:fixed; top:30%; left:50%; margin-left:-45%; z-index:901;
}
.white_d{
	height:auto; background-color:#FFF; margin:10px; padding-bottom:20px;
}
.notice_t{
	width:100%; height:45px; line-height:45px; background-color:#ff4e00; overflow:hidden; color:#FFF; font-size:1.3em; text-align:center; font-family:"Microsoft YaHei"; margin:0 auto; position:relative;
}
.notice_c{
	width:94%; height:auto; margin-top:10px;
}
.notice_c table tr td{
	font-size:1.2em;
}
.notice_c table tr td font{
	color:#ff4e00; font-size:1.3em;
}

a.b_sure{
	width:120px; height:40px; line-height:40px; overflow:hidden; background-color:#ff4e00; color:#FFF; font-size:1.2em; font-family:"Microsoft YaHei"; text-align:center; display:inline-block; margin-right:10px; border:0;
}
a.b_sure:hover{
	color:#FFF;
}
.b_buy{
	width:118px; height:38px; line-height:38px; overflow:hidden; background-color:#f6f6f6; color:#ff4e00; font-size:14px; font-family:"Microsoft YaHei"; text-align:center; float:left;  margin-right:10px; border:1px solid #eaeaea;
}
.b_buy:hover{
	color:#ff4e00;
}
/*end  mask layer  end*/


.p_des{
	overflow:hidden; padding:1% 0; border-top:1px solid #cccccc;
}
.p_name{
	color:#333333; font-size:1.6em; padding-bottom:2%;
}
.p_spe{
	color:#888888; font-size:1.3em; padding-bottom:3%;
}
.p_con{
	font-size:1.3em; padding:1% 0;
}
.p_con font{
	color:#ff4e00; font-size:1.5em;
}
.p_con ul li{
	height:28px; line-height:28px; overflow:hidden; font-size:14px; text-align:center; font-family:"Microsoft YaHei"; float:left;  margin-right:10px; margin-top:4px; margin-bottom:2px; padding:0 15px; border:1px solid #cccccc; cursor:pointer; position:relative;
}
.p_con ul li:hover{
	height:26px; line-height:26px; padding:0 14px; overflow:hidden; border:2px solid #ff4e00;
}
.p_con ul li .ch_img{
	width:18px; height:18px; overflow:hidden; background:url(/image/ch.png) no-repeat center top; position:absolute; right:0; bottom:0; display:none;
}
.p_con ul li.checked{
	height:26px; line-height:26px; padding:0 14px; overflow:hidden; border:2px solid #ff4e00;
}
.p_con ul li.checked .ch_img{
	display:block; color:#ff4e00;
}
.like{
	overflow:hidden;
	margin-top:10px;
}
.like ul li{
	width:45%; overflow:hidden; background-color:#FFF; float:left; margin:0 2% 4% 2%; border:1px solid #eaeaea;
}
.like ul li .img{
	width:63.5%; overflow:hidden; margin-top:1%;
}
.like ul li .img img{
	width:100%;
}
.like ul li .name{
	height:40px; line-height:20px; overflow:hidden; color:#888888; font-size:12px; text-align:center; margin-top:1%;
}
.like ul li .name h2{
	height:20px; line-height:20px; overflow:hidden; color:#555555; font-size:16px; text-align:center;
}
.like ul li .name h2 a{
	color:#555555;
}
.like ul li .name h2 a:hover{
	color:#555555; text-decoration:underline;
}
.like ul li .name a{
	color:#888888;
}
.like ul li .name a:hover{
	color:#888888; text-decoration:underline;
}
.like ul li .price{
	height:30px; line-height:30px; overflow:hidden; color:#999999; text-align:center; text-transform:uppercase;
}
.like ul li .price font{
	color:#ff4e00; font-size:14px;
}
.like ul li .price span{
	color:#ff4e00; font-size:20px;
}
